import React, { Component } from 'react';
import {connect} from 'react-redux';
import { Route } from 'react-router-dom';

import Layout from './hoc/Layout/Layout';
import asyncComponent from './hoc/asyncComponent';
import * as actions from './store/actions/auth'
const asyncTechnews = asyncComponent(() => {
  return import('./containers/technews/technews')
})
const asyncHacktools = asyncComponent(() => {
  return import('./containers/hacktools/hacktools')
})

class App extends Component {
  componentDidMount() {
    this.props.onCheckAuthState()
  }
  render() {
    return (
        <Layout>
        <Route path='/technews' component={asyncTechnews}/>
        <Route path='/hacktools' component={asyncHacktools}  />
       </Layout>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
      onCheckAuthState : () => dispatch(actions.checkAuthState())
  }
}

export default connect(null,mapDispatchToProps)(App);



