import React, { Component } from 'react';
import '../../css/materialize.css'

class Layout extends Component  {
    render() {
        return (
           <React.Fragment> 
                <main>
                    {this.props.children}
                </main>        
            </React.Fragment>
        );
    }
}

export default Layout;


